.header {
	position: relative;
	height: 40px;
	padding: 5px;
	min-width: 305px;

	.menu {
		a {
			text-decoration: none;
			position: absolute;
			left: 10px;
		}

		ul {
			list-style: none;
		}

		ul li {
			display: inline;
		}

		.section {
			position: absolute;
			right: 10px;
		}
	}
	}
